import { Injectable } from '@angular/core';

import { HttpClient } from '@angular/common/http';

import { Observable } from 'rxjs';
import { Resetpassword } from '../models/resetpassword';
import * as vglobales from 'globales';
@Injectable({
  providedIn: 'root'
})
export class ResetpasswordService {

  url = vglobales.lurl+'/api/resetpassword/';

  constructor(private http: HttpClient) { }

  getSolicitudes(): Observable<any> {
    return this.http.get(this.url);
  }

  eliminarSolicitudes(id: string): Observable<any> {
    return this.http.delete(this.url + id);
  }

  guardarSolicitudes(resetpassword: Resetpassword): Observable<any> {
    return this.http.post(this.url, resetpassword);
  }

  obtenerSolicitud(id: string): Observable<any> {
    return this.http.get(this.url + id);
  }
}


